import React from 'react';
import Pager from '../../common/pager';
import {
  Content
} from './style';

const UI = ({data, index, allNum}) => (
  <div className="home">
    <Content>
      <div className="resource-list">
        <ul>
          {
            data.map(item => (
              <li key={item.id}>
                <h1>{item.title}</h1>
                <div className="info">
                  <p>
                    <span><i className="iconfont">&#xe61d;</i> {item.ower} </span>
                    <span><i className="iconfont">&#xe6a9;</i> {item.date} </span>
                    <span><i className="iconfont">&#xe65d;</i> {item.views} </span>
                    <span><i className="iconfont">&#xe616;</i> {item.comments}次浏览 </span>
                  </p>
                </div>
                <div className="desc">
                  <div className="img-box">
                    <img src={item.imgUrl} alt={item.dec}/>
                  </div>
                  <p>{item.desc}</p>
                </div>
              </li>
            ))
          }
        </ul>
        <Pager
          type='home'
          num={allNum}
          index={index}
        />
      </div>
    </Content>
  </div>
);

export default UI;
