import styled from 'styled-components';

export const Content = styled.div`
  padding: 30px 50px;
  /* display: flex;
  justify-content: space-between; */
  .resource-list {
    width: 780px;
    border: 1px solid #ccc;
    border-radius: 6px;
    ul {
      padding: 15px;
    }
    li {
      padding-bottom: 10px;
      border-bottom: 1px solid #ccc;
      h1 {
        margin: 10px 0 15px;
        font-size: 18px;
        color: #337ab7;
        line-height: 34px;
        cursor: pointer;
      }
      .info {
        margin-bottom: 30px;
        font-size: 12px;
        color: #999;
        span {
          margin: 0 4px;
        }
      }
      .desc {
        display: flex;
        justify-content: flex-start;
        .img-box {
          border: 1px solid #f2f2f2;
          border-radius: 4px;
          padding: 2px;
          cursor: pointer;
          transition: .3s;
          &:hover {
            border-color: #ccc;
          }
        }
        p {
          margin-left: 20px;
          line-height: 1.4;
        }
      }
      
    }
  }

  @media screen and (min-width: 990px) and (max-width: 1200px) {
    padding: 0 50px;
    .resource-list {
      width: 600px;
    }
  }
  @media screen and (min-width: 768px) and (max-width: 989px) {
    padding: 0 5%;
    .resource-list {
      width: 100%;
    }
  }
  @media screen and (max-width: 767px) {
    .resource-list {
      width: auto;
    }
    padding: 80px 20px;
    h1 {
      font-size: 16px;
    }
    .desc {
      .img-box {
        display: none;
      }
      p {
        font-size: 14px;
        margin-left: 0 !important;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    
  }
`;
