import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from './store/actions';
import UI from './UI'

class Home extends Component {
  render () {
    const { data, index, allNum } = this.props;

    return (
      <UI
        data={data}
        index={index}
        allNum={allNum}
      />
    );
  }

  componentDidMount () {
    const { index, handleMount } = this.props;
    handleMount(index);
  }
};

const mapState = state => ({
  index: state.home.get('homePagerIndex'),
  allNum: state.home.get('homePagerAllNum'),
  data: state.home.get('data').toJS()
});

const mapDispatch = dispatch => ({
  // 首页初始加载第index页的数据
  handleMount (index) {
    dispatch(actions.getHomeData(index));
  }
});

export default connect(mapState, mapDispatch)(Home);
