import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from './store/actions'
import {
  PagerWrapper
} from './style';

class Pager extends Component {
  render () {
    const { type, num, currentPage, handleClick } = this.props;

    return (
      <PagerWrapper>
        {
          currentPage > 1 && <button className='prev' onClick={() => { handleClick(type, currentPage - 1) }}>上一页</button>
        }
        {
          currentPage < num && <button className='next' onClick={() => { handleClick(type, currentPage + 1) }}>下一页</button>
        }
        <p>{currentPage} / {num}</p>
      </PagerWrapper>
    );
  }
}

const mapState = state => ({
  currentPage: state.pager.get('index')
});

const mapDispatch = dispatch => ({
  handleClick (type, nth) {
    dispatch(actions.changePage(type, nth));
  }
});

export default connect(mapState, mapDispatch)(Pager);
